import Vue from 'vue'
import Vuex from 'vuex'
import Selection from 'gantt-schedule-timeline-calendar/dist/Selection.plugin.js'
// import clickAction from './selectblock'
import WeekendHighlight from './weekendhighlight'
import store from './store'
// import { html } from 'lit-html'

function openBooking (element, data) {
  function onClick (event) {
    store.commit('OPEN_POPUP', data)
  }
  element.addEventListener('click', onClick)
  return {
    update (element, newData) {
      data = newData
    },
  }
}

function clickAction (element, data) {
  function onClick (event) {
    const dataSet = {
      room: '',
      inDate: '',
      outDate: '',
    }
    let i = 0
    let canSelect = false
    for (const block of data.blocks) {
      if (block.selected) {
        canSelect = true
        if (i === 0) {
          dataSet.inDate = new Date(block.time.rightGlobal).toISOString().substr(0, 10)
          i++
        }
        dataSet.outDate = new Date(block.time.rightGlobal).toISOString().substr(0, 10)
        dataSet.room = block.row.id
      }
    }
    if (canSelect) {
      clearData()
      store.commit('SET_BOOKING_OPEN', dataSet)
    }
  }
  element.addEventListener('click', onClick)
  return {
    update (element, newData) {
      data = newData
    },
  }
}
function getLabel (status) {
  if (status === 'pre-reserve' || status === 'reserve') {
    return 'Резерв ' + store.state.client.phone
  } else if (status === 'pre-booking' || status === 'booking') {
    return store.state.client.lastName + ' ' + store.state.client.firstName
  } else if (status === 'populate') {
    return store.state.client.lastName + ' ' + store.state.client.firstName
  } else if (status === 'move-out') {
    return store.state.client.lastName + ' ' + store.state.client.firstName
  }
  return store.state.client.lastName + ' ' + store.state.client.firstName
}
function clearData () {
  store.state.reservSource = ''
  store.state.reserveNumber = ''
  store.state.earlyIn = ''
  store.state.latelyOut = ''
  store.state.sleepingArea = ''
  store.state.noteType = ''
  store.state.freeRoom = ''
  store.state.cost = ''
  store.state.category = ''
  store.state.snackbar = false
  store.state.text = ''
  store.state.client.email = ''
  store.state.client.firstName = ''
  store.state.client.lastName = ''
  store.state.client.patronymic = ''
  store.state.client.gender = ''
  store.state.client.passport = ''
  store.state.client.phone = ''
}
function getColor (status) {
  if (status === 'pre-reserve' || status === 'reserve') {
    return 'red'
  } else if (status === 'pre-booking' || status === 'booking') {
    return 'yellow'
  } else if (status === 'populate') {
    return 'green'
  } else if (status === 'move-out') {
    return 'lightgray'
  }
}
function getRowId (status) {
  return parseInt(store.state.room) + ''
}
Vue.use(Vuex)
const endDay = new Date()
const beginDay = new Date()

const rows = {}
for (let i = 0; i < 105; i++) {
  const id = i.toString()
  if (i < 3) {
    rows[id] = {
      id,
      label: i === 0 ? 'Люкс' : i === 1 ? 'Полюлюкс' : 'Стандарт',
      canSelect: false,
      style: {
        current: { background: 'lightgray', 'font-weight': 'bold' },
        grid: {
          block: {
            current: { background: 'lightgray', 'font-weight': 'bold' },
          },
        },
      },
    }
  } else if (i > 3 && i < 34) {
    rows[id] = {
      id,
      label: `Комната: ${i - 3}`,
      parentId: '0',
      canSelect: true,
    }
  } else if (i > 33 && i < 64) {
    rows[id] = {
      id,
      label: `Комната: ${i - 33}`,
      parentId: '1',
      canSelect: true,
    }
  } else if (i > 63 && i < 94) {
    rows[id] = {
      id,
      label: `Комната: ${i - 63}`,
      parentId: '2',
      canSelect: true,
    }
  } else if (i > 99 && i < 104) {
  }
}
let id = 94
rows[id] = {
  id,
  label: 'Койка: 1',
  expanded: false,
  parentId: '4',
  canSelect: true,
}
id = 95
rows[id] = {
  id,
  label: 'Койка: 2',
  expanded: false,
  parentId: '4',
  canSelect: true,
}
id = 96
rows[id] = {
  id,
  label: 'Койка: 3',
  expanded: false,
  parentId: '4',
  canSelect: true,
}
id = 97
rows[id] = {
  id,
  label: 'Койка: 4',
  expanded: false,
  parentId: '4',
  canSelect: true,
}
id = 98
rows[id] = {
  id,
  label: 'Койка: 5',
  expanded: false,
  parentId: '4',
  canSelect: true,
}
/*
all statuses
pre-reserve
pre-booking
reserve
booking
populate
move-out
edit
repair
 */
export default new Vuex.Store({
  state: {
    barColor: 'rgba(0, 0, 0, .8), rgba(0, 0, 0, .8)',
    barImage: 'https://demos.creative-tim.com/material-dashboard/assets/img/sidebar-1.jpg',
    drawer: null,
    bookingModal: false,
    booking: null,
    reserve: false,
    config: {
      height: screen.height - 450,
      list: {
        rows,
        columns: {
          data: {
            label: {
              id: 'label',
              data: 'label',
              expander: true,
              isHTML: false,
              width: 230,
              minWidth: 100,
              header: {
                content: 'Комнаты ',
              },
            },
          },
        },
        toggle: {
          display: false,
        },
      },
      chart: {
        spacing: 1,
        items: {
          5: {
            id: '5',
            rowId: '4',
            label: 'Саша',
            firstName: 'Саша',
            lastName: 'Саша',
            patronymic: 'Саша',
            moveable: false,
            resizeable: false,
            status: 'booking',
            reservSource: 'Instagram',
            noteType: 'undefined',
            earlyIn: false,
            earlyOut: false,
            phone: '7-777-777-77-77',
            email: 'test@test.test',
            cost: 1000,
            paid: 1000,
            style: {
              background: '#ffc800',
            },
            time: {
              start: new Date().getTime() + 12 * 24 * 60 * 60 * 1000,
              end: new Date().getTime() + 14 * 24 * 60 * 60 * 1000,
            },
          },
        },
        time: {
          calculatedZoomMode: true,
          from: beginDay.setHours(0, 0, 0),
          to: endDay.setDate(endDay.getDate() + 29), // 18400000, // +60 days 5097600000,
          leftGlobal: new Date().getDay(),
        },
      },
      plugins: [
        Selection({
          items: false,
          rows: true,
          grid: false,
          rectStyle: { opacity: '0.1' },
          canSelect (type, currentlySelecting) {
            if (type === 'chart-timeline-grid-row-block') {
              return currentlySelecting.filter(selected => {
                if (!selected.row.canSelect) return false
                for (const item of selected.row._internal.items) {
                  if (
                    (item.time.start >= selected.time.leftGlobal && item.time.start <= selected.time.rightGlobal) ||
                    (item.time.end >= selected.time.leftGlobal && item.time.end <= selected.time.rightGlobal) ||
                    (item.time.start <= selected.time.leftGlobal && item.time.end >= selected.time.rightGlobal)
                  ) {
                    return false
                  }
                }
                return true
              })
            }

            return currentlySelecting
          },
          canDeselect (type, currently, all) {
            if (type !== 'chart-timeline-grid-row-blocks') {
              return all.selecting['chart-timeline-grid-row-blocks'].length ? [] : currently
            }
            return []
          },
          selecting (data, type) {
          },
          deselecting (data, type) {
          },
          selected (data, type) {
          },
          deselected (data, type) {
          },
        }),
        WeekendHighlight({
          weekdays: [6, 0], // Saturnday, Sunday
          className: 'weekends-highlight',
        }),
      ],
      actions: {
        'chart-timeline-items-row-item': [openBooking],
        'chart-timeline-grid-row': [clickAction],
      },
      locale: {
        name: 'ru',
        weekdays: 'ВС_ПН_ВТ_СР_ЧТ_ПТ_СУ'.split('_'),
        weekdaysShort: 'ВС_ПН_ВТ_СР_ЧТ_ПТ_СУ'.split('_'),
        weekdaysMin: 'ВС_ПН_ВТ_СР_ЧТ_ПТ_СУ'.split('_'),
        months: 'Январь_Февраль_Март_Апрель_Май_Июнь_Июль_Август_Сентябрь_Октябрь_Ноябрь_Декабрь'.split('_'),
        monthsShort: 'Янв_Фев_Мап_Апр_Май_Июн_Июл_Авг_Сен_Окт_Ноя_Дек'.split('_'),
        weekStart: 1,
      },
    },
    start: null,
    end: null,
    room: null,
    toBook: null,
    refs: null,
    popover: false,
    label: '',
    rowLabel: '',
    status: '',
    reservSource: '',
    reserveNumber: '',
    earlyIn: '',
    latelyOut: '',
    sleepingArea: '',
    noteType: '',
    freeRoom: '',
    cost: '1000',
    category: '',
    client: {
      firstName: '',
      lastName: '',
      patronymic: '',
      phone: '',
      email: '',
      passport: '',
      gender: '',
      guest: '',
    },
    snackbar: false,
    text: '',
    services: {
      sale: {
        basePrice: 1241,
        birthdayPrice: 1000,
        partnerPrice: 1900,
        weekdaysPrice: 3900,
      },
      lunch: 2000,
      additionalSeats: 10000,
      rentalBike: 1000,
      weekdayBath: 600,
      managementDiscretion: 700,
      reverse: 900,
      breakfast: 1500,
      dinner: 2400,
      earlyIn: 200,
      masterClass: 1400,
      shootInTir: 500,
      bathUnlim: 1000,
      excursion: 1000,
    },
  },
  mutations: {
    SET_BAR_IMAGE (state, payload) {
      state.barImage = payload
    },
    SET_DRAWER (state, payload) {
      state.drawer = payload
    },
    SET_BOOKING_OPEN (state, payload) {
      state.bookingModal = !state.bookingModal
      state.room = payload.room
      state.start = payload.inDate
      state.end = payload.outDate
    },
    SET_RESERVE_DATA (state, payload) {
      store.commit('SET_DATA', 'reserve')
    },
    SET_BOOKING_DATA (state, payload) {
      store.commit('SET_DATA', 'booking')
    },
    SET_DATA (state, payload) {
      var obj = {}
      var id
      if (state.room !== '') {
        id = state.room
      } else {
        id = Object.keys(state.config.chart.items).length + 1
      }
      obj.id = '' + id
      obj.label = getLabel(payload)
      obj.firstName = state.client.firstName
      obj.lastName = state.client.lastName
      obj.patronymic = state.client.patronymic
      obj.phone = state.client.phone
      obj.email = state.client.email
      obj.passport = state.client.passport
      obj.gender = state.client.gender
      obj.moveable = false
      obj.resizeable = false
      obj.reservSource = state.reservSource
      obj.reservNumber = state.reserveNumber
      obj.earlyIn = state.earlyIn
      obj.latelyOut = state.latelyOut
      obj.sleepingArea = state.sleepingArea
      obj.noteType = state.noteType
      obj.freeRoom = state.freeRoom
      obj.cost = state.cost
      obj.status = payload
      obj.category = state.category
      obj.style = {}
      obj.style.background = getColor(payload)
      obj.rowId = getRowId(payload) // parseInt(state.room) + 3 + ''
      obj.style.color = 'black'
      obj.time = {}
      obj.time.start = Date.parse(state.start)
      obj.time.end = Date.parse(state.end) + 60 * 60 * 1000
      if (state.popover) {
        state.popover = false
      }
      if (state.toBook) {
        state.toBook = false
      }
      if (state.bookingModal) {
        state.bookingModal = false
      }
      state.bookingModal = !state.bookingModal
      state.bookingModal = !state.bookingModal
      Vue.set(state.config.chart.items, id, obj)
      state.reserve = !state.reserve
      clearData()
    },
    OPEN_CLOSE_RESERVE (state, payload) {
      if (payload.status === 'repair') {
        var obj = {}
        var id = Object.keys(state.config.chart.items).length + 1
        obj.id = '' + id
        obj.rowId = state.room
        obj.label = 'Закрыто на ремонт'
        obj.moveable = false
        obj.resizeable = false
        obj.status = payload.status
        obj.style = {}
        obj.style.background = 'gray'
        obj.time = {}
        obj.time.start = Date.parse(state.start)
        obj.time.end = Date.parse(state.end) + 2 * 60 * 60 * 1000
        Vue.set(state.config.chart.items, id, obj)
        state.reserve = !state.reserve
      }
      state.bookingModal = !state.bookingModal
      state.reserve = !state.reserve
      state.status = payload.status
    },
    OPEN_CLOSE_BOOK (state, payload) {
      state.reserve = !state.reserve
      state.status = 'booking'
    },
    SET_REFS (state, payload) {
      state.refs = payload
    },
    PREV_DAY (state, payload) {
      const begDay = new Date(state.config.chart.time.from)
      const endDay = new Date(state.config.chart.time.to)
      state.config.chart.time.from = begDay.setDate(begDay.getDate() - 1)
      state.config.chart.time.to = endDay.setDate(endDay.getDate() - 1)
    },
    NEXT_DAY (state, payload) {
      const begDay = new Date(state.config.chart.time.from)
      const endDay = new Date(state.config.chart.time.to)
      state.config.chart.time.from = begDay.setDate(begDay.getDate() + 1)
      state.config.chart.time.to = endDay.setDate(endDay.getDate() + 1)
    },
    CHANGE_PERIOD (state, payload) {
      const day = new Date(payload.today)
      state.config.chart.time.leftGlobal = state.config.chart.time.from = day.setDate(day.getDate())
      state.config.chart.time.to = day.setDate(day.getDate() + payload.period)
    },
    ADD_ACTIONS (state, payload) {
      state.config.actions = {
        'chart-timeline-items-row-item': [openBooking],
        'chart-timeline-grid-row': [clickAction],
      }
    },
    OPEN_POPUP (state, payload) {
      state.status = payload.item.status
      state.reservSource = payload.item.reservSource
      state.start = new Date(payload.item.time.start).toISOString().substr(0, 10)
      state.end = new Date(payload.item.time.end).toISOString().substr(0, 10)
      state.startTime = new Date(payload.item.time.start).toLocaleTimeString()
      state.endTime = new Date(payload.item.time.end).toLocaleTimeString()
      state.popover = !state.popover
      state.label = payload.item.label
      state.earlyIn = payload.item.earlyIn
      state.room = payload.item.id
      state.latelyOut = payload.item.latelyOut
      state.rowLabel = payload.row.label
      state.noteType = payload.item.noteType
      state.client.firstName = payload.item.firstName
      state.client.lastName = payload.item.lastName
      state.client.patronymic = payload.item.patronymic
      state.client.passport = payload.item.passport
      state.client.phone = payload.item.phone
      state.client.email = payload.item.email
      state.client.gender = payload.item.gender
    },
    SET_RESERVE (state, payload) {
      store.commit('SET_DATA', 'reserve')
    },
    SET_POPULATE (state, payload) {
      state.config.chart.items[state.room].style.background = 'green'
      state.config.chart.items[state.room].style.color = 'white'
      state.config.chart.items[state.room].status = 'populate'
      state.reserve = !state.reserve
      state.popover = !state.popover
    },
    UNSET_POPULATE (state, payload) {
      state.config.chart.items[state.room].style.background = 'lightgray'
      state.config.chart.items[state.room].status = 'move-out'
      state.reserve = !state.reserve
      state.popover = !state.popover
    },
    DELETE (state, payload) {
      Vue.delete(state.config.chart.items, state.room)
      state.reserve = !state.reserve
      state.popover = !state.popover
    },
  },
  actions: {
    scrollTo (gstc, time) {
      gstc.scrollToTime(gstc.api.date(time))
    },
  },
})
